.container1 {
  position: relative;
  width: 16%;
  overflow:hidden;
  margin: 10px;
  margin-top: -80px;
}

.container2 {
  position: relative;
  width: 28%;
  overflow:hidden;
  margin: 10px;
  margin-top: -80px;
}

.image {
  display: block;
  width: 100%;
  height: auto;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  transition: 2s;
  overflow:hidden;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 2s;
}

.container1:hover .overlay {
  height: 100%;
  cursor: pointer;
}

.container2:hover .overlay {
  height: 100%;
  cursor: pointer;
}

.container1:hover .image {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  transition: 2s;
  transform:scale(1.2); 
}

.container2:hover .image {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  transition: 2s;
  transform:scale(1.2); 
}

.text {
