.git{
  margin-top: 5px;
  margin-left: 10px;
  font-size:25px;
  transition: 350ms;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 15px 20px;
  position: relative;
  -webkit-filter: blur(1px);
          filter: blur(1px);

}

.git:after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.git:hover:after { 
  width: 100%; 
  left: 0; 
}
@media screen and (max-height: 300px) {
	ul {
		margin-top: 40px;
	}
}

.git:hover{
  color: white;
  transition: 350ms;
  -webkit-filter: blur(0px);
          filter: blur(0px);
}

.wrapper{
	display: none;
	position: absolute;
