.btn{
	margin-top: 150px;
	width: 110px;
	color: #2196F3;
	height: 40px;
	cursor: pointer;
	border: #2196F3 solid 1.5px; 
	background: #fff;
	transition: 500ms;
}

.btn:hover{
	border: #2196F3 solid 1.5px; 
	background: #2196F3;
	color: #fff;
	border: #2196F3 solid 1.5px;
	transition: 500ms;
}

.loading-dots > div{
	width: 1.5rem;
	height: 1.5rem;
	background-color: #727272;
	border-radius: 50%;
	display: inline-block;
	margin-top: 200px;
	animation: 1.2s bounce infinite ease-in-out both;
}

.loading-dots .bounce{
	animation-delay: -0.3s;
}

.loading-dots .bounce2{
	animation-delay: -0.15s;
}

.loading-dots .bounce3{
	animation-delay: 0s;
}

@keyframes bounce{
	0%, 80%, 100%{
		transform: scale(0.1);
	}
	40%{
		transform: scale(1);
	}
}

